<template>
  <header  id="header" class="headroom">
    <div class="header-warp">
      <div style="overflow: hidden">
        <div class="nav my_name">MMapp</div>
        <ul class="head-center nav clearfix" style="float: right">
          <li><a href="#">主页</a></li>
          <li><a href="http://www.baidu.com" target="_blank">百度</a></li>
          <li><a href="http://www.taobao.com" target="_blank">淘宝</a></li>
          <li><a href="#">设置</a></li>
        </ul>
      </div>
      <img :src="log_img" class=" img_size" alt="MMApp"  >
      <div id="navbar" class="clearfix">
        <ul class="nav router_font clearfix">
          <li><router-link to="/home">Home</router-link></li>
          <li><router-link to="/about">About</router-link></li>
          <li><a href="#">上班</a></li>
          <li><a href="#">睡觉</a></li>
          <li><router-link to="/eat">吃饭</router-link></li>
