<script>
  export default {
    data(){
      return{
        log_img:require("../static/img/log.jpg")
      }
    }
  }
</script>

<style scoped>
  .router_font{
    font-size:20px;
    margin: 0;
  }
  .router_font>li::after{
    content: "";
    margin-left: 30px;
    border-left:#ccc dashed 1px;
  }
  #navbar{
    height: 40px;
    width: 80%;
    float: right;
    margin-top: 170px;
  }
  #navbar>ul{
    margin-left: 90px;
  }
  .img_size{
    height: 200px;
    width: 200px;
  }
  #header{

    overflow: hidden;
    /*border-radius: 10px;*/
    /*border: 1px solid cadetblue;*/
    /*border-top: none;*/

  }
  .nav{
    float: left;
  }
  .nav>li{
    float: left;
    margin: 0 15px;
  }
  .header-warp{
    width: 1000px;
    height: 100%;
    margin: auto;
    overflow: hidden;
  }

  .head-center{
    margin: 15px;
  }
  .my_name{
    font: 20px '微软雅黑';
    margin: 8px 4px 0px 60px;
  }
</style>



