/*
 路由器模块
 */
import Vue from 'vue'
import VueRouter from "vue-router"

import About from "../views/About.vue"
import Home from "../views/Home.vue"
import Eat from "../views/Eat.vue"


Vue.use(VueRouter)
export default new VueRouter({
  routes:[
    {
      path:'/about',
      component:About
    },
    {
      path:'/home',
      component:Home
    },
    {
      path:'/eat',
      component:Eat
    },
    {
      path:'/',
      redirect:'/home'
    }
  ]
})



