<template>
  <div class="block">
    <el-carousel height="500px">
      <el-carousel-item v-for="item in items" :key="item">
        <img :src="item.img" alt="">
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        items: [
          {img: require('../static/img/move_img1.jpg')},
          {img: require('../static/img/move_img2.jpg')},
          {img: require('../static/img/move_img3.jpg')},
          {img: require('../static/img/move_img4.jpg')}
        ]
      }
    }
  }
</script>


<style scoped>
  .block img{

    width: auto;
    max-width: 100%;

  }

  @media screen and (max-width:900px){
    .block{
      display: none;
    }
  }
</style>



