import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
const state = {
   count:1//初始化状态
}
const mutations = {
  CHANGE(state){
    state.count+=1;
  }
}
const actions = {
  change({commit}) {
    commit('CHANGE');
  }
}
const getters = {
  add(state){
    return state.count%2===0?'偶数':'基数';
  }
}
export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
})



